<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Daftar Username',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Daftar Username',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false,
			modeltampil: "100",
			datatampil: [],
			modelsearch: "",
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdata()
    },
	computed: {
      pilihmodeltampil() {
        return this.Getdata(this.modeltampil);
      },
      pilihmodelsearch() {
        return this.Getdata(this.modeltampil, this.modelsearch);
      },
    },
	methods: {
      async Getdata(tampil, search) {
        try {
            let kodetampil = tampil ? tampil : 100;
            let kodesearch = search ? search : "";
            let response = await axios.get(this.api+"/username/index?search="+kodesearch +
            "&tampil=" +
            kodetampil, Utils.authHeader());
            this.data = response.data;
        } catch (err) {
            console.error(err);
        }
      },
      async openEdit(id) {
          document.location = '/usernameaction/edit/' + id
      },
      async openDetail(id) {
          document.location = '/usernameaction/detail/' + id
      },
      async openRemove(id) {
		if (confirm('Anda Yakin akan menghapus Data')) {
		const item = this.data.find((el) => el.f_password_id === id);
        const res =  await axios.delete(this.api+"/username/delete/"+item.f_password_id, Utils.authHeader());
		this.alert = true
        this.message = res.data.message
        this.Getdata()
		} else {
        
		}
	  }
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									<br>&nbsp;&nbsp;
									<b-button variant="dark" to="/usernameaction/tambah">Tambah Data</b-button>
									<br>
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>
									<br>
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tampil"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modeltampil"
										type="number"
										required
										@click="pilihmodeltampil"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Data / Username"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelsearch"
										type="text"
										required
										@click="pilihmodelsearch"
									></b-form-input>
									</b-form-group>
									<br>
									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Id</th>
											<th scope="col">Username</th>
											<th scope="col">Nama</th>
											<th scope="col">Status</th>
											<th scope="col">Start Login</th>
											<th scope="col">Akhir Login</th>
											<th scope="col">Action</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_password_id">
											<th scope="row">{{index+1}}</th>
											<td>{{tampil.f_password_id}}</td>
											<td>{{tampil.f_username}}</td>
											<td>{{tampil.f_nama}}</td>
											<td>{{tampil.f_statusaktif == '1' ? 'Tidak Aktif' : 'Aktif' }} </td>
											<td>{{tampil.f_startdate}}</td>
											<td>{{tampil.f_lastlogin}}</td>
											<td>
											<b-button variant="info" @click="openDetail(tampil.f_password_id)">Detail Data</b-button>
											&nbsp;
											<b-button variant="warning" @click="openEdit(tampil.f_password_id)">Edit Data</b-button>
											&nbsp;
											<b-button variant="danger" @click="openRemove(tampil.f_password_id)">Delete Data</b-button>
											</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
